.header {
  position: relative;
  width: 100%;
  height: 44px;
  background: #FFFFFF;
  -webkit-box-shadow: 0 1px 0 0 #B2B2B2;
          box-shadow: 0 1px 0 0 #B2B2B2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

p.title_back {
  position: absolute;
  top: 0em;
  left: 0;
  width: 6.4em;
  height: 44px;
  line-height: 44px;
  text-indent: 5px;
}

p.title_back i {
  color: #0076FF;
  font-size: 17px;
  font-weight: 700;
}

p.title_back span {
  font-family: SFUIText-Regular;
  font-size: 17px;
  color: #0076FF;
  letter-spacing: -0.41px;
}

h2.title {
  width: 8.75em;
  height: 44px;
  background: #fff;
  font-family: SFUIText-Semibold;
  font-size: 17px;
  color: #030303;
  letter-spacing: -0.41px;
  line-height: 44px;
  text-align: center;
}

div.main {
  width: 100%;
  height: auto;
  background: #fff;
}

div.mainWrap {
  padding: 0 .75em;
}

h4.tag {
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #666666;
  letter-spacing: 0;
  font-weight: 400;
  margin: .55em 0 0 0;
  line-height: 1.4em;
}

div.idea {
  width: 100%;
  position: relative;
  margin-bottom: 1em;
}

h2.mainTile {
  font-family: PingFangSC-Medium;
  font-size: 18px;
  color: #333333;
  letter-spacing: 0;
  line-height: 18px;
  line-height: 3em;
}

textarea.ideaText {
  margin: 0;
  padding: .5em;
  width: 95%;
  height: 12em;
  background: rgba(204, 204, 204, 0.20);
  border-radius: 4px;
  resize: none;
  font-family: PingFangSC-Regular;
  font-size: 16px;
  color: #666666;
  letter-spacing: 0;
  line-height: 16px;
}

span.limit {
  position: absolute;
  right: .5em;
  bottom: .5em;
  width: 5em;
  height: 1.5em;
  text-align: center;
  line-height: 1.5em;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #999999;
  line-height: 12px;
}

div.upload {
  height: 19.5em;
}

.uploadFile {
  position: relative;
  display: block;
  overflow: hidden;
  text-align: center;
  width: auto;
  background-color: #f5f5f5;
  border: solid 1px #ddd;
  border-radius: 4px;
  line-height: 18px;
  text-decoration: none;
  width: 12em;
  height: 12em;
  line-height: 12em;
}

.uploadFile i {
  color: #cccccc;
  font-size: 50px;
}

.uploadFile input[type="file"] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  filter: alpha(opacity=0);
}

div.submit button {
  width: 100%;
  height: 50px;
  background: #FE6900;
  border-radius: 8px;
  border: none;
  font-family: PingFangSC-Regular;
  font-size: 18px;
  color: #FFFFFF;
  letter-spacing: 0;
}

div.noticeWrap {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

div.notice {
  width: 245px;
  height: 195px;
  background: transparemt;
  border-radius: 8px;
  text-align: center;
}

div.notice img {
  z-index: 9;
  width: 105px;
  height: 105px;
  border: 5px solid #fff;
  border-radius: 50%;
}

div.noticeMain {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index:-1;
  width: 245px;
  height: 144px;
  margin: -46.5px 0 0 -123px;
  background: #FFFFFF;
  -webkit-box-shadow: 0 2px 6px 2px rgba(0, 0, 0, 0.20);
          box-shadow: 0 2px 6px 2px rgba(0, 0, 0, 0.20);
  border-radius: 8px;
}

div.noticeContent {
  width: 100%;
  height: auto;
  margin-top: 75px;
  text-align: center;
  line-height: 3.5em;
}

p.noticeS {
  font-family: PingFangSC-Medium;
  font-size: 18px;
  color: #FE6900;
  letter-spacing: 0;
  line-height: 20px;
}

p.noticeL {
  width: 205px;
  height: 40px;
  margin: 0 auto;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #999999;
  letter-spacing: 0;
  line-height: 20px;
}

p.noticeL a {
  font-family: PingFangSC-Medium;
  font-size: 14px;
  color: #2A2A2A;
  letter-spacing: 0;
}



